import React, { useEffect, useState } from "react";
import ImgBanner from './assets/banner.svg';
import "./styles.css";

export const handler = ({ inputs, mechanic }) => {
  const {
    subtitel,
    subtitel2,
    titel,
    titel2,
    untertitel,
    sponsoring,
  } = inputs;

  const { topLogo, bottomLogo } = inputs;
  const [topHref, setTopHref] = useState("");
  const [bottomHref, setBottomHref] = useState("");

  const loadImageFromFileObject = (fileObject, stateSetter) => {
    const reader = new FileReader();
    reader.onload = () => {
      stateSetter(reader.result);
    };
    reader.readAsDataURL(fileObject);
  };

  useEffect(() => {
    if (topLogo) loadImageFromFileObject(topLogo, setTopHref);
    if (bottomLogo) loadImageFromFileObject(bottomLogo, setBottomHref);
  }, []);

  useEffect(() => {
    if (topHref && bottomHref) mechanic.done();
  }, [topHref, bottomHref]);

  return (
    <svg width={1200} height={627}>
      <rect width={1200} height={627} />
        <image href={ImgBanner} width={1200} height={627} />
        <image
          x={950}
          y={-10}
          width={200}
          height={200}
          href={topHref}
        >
        </image>

        <image
          x={200}
          y={447}
          width={230}
          height={230}
          href={bottomHref}
        >
        </image>
        {/* The text */}

        <text
          x={25}
          y={75}
          fill={'#ffffff'}
          textAnchor="start"
          fontWeight="regular"
          fontFamily="Garnett Regular"
          fontSize={30}
        >
          {subtitel}
        </text>

        <text
          x={25}
          y={125}
          fill={'#ffffff'}
          textAnchor="start"
          fontWeight="regular"
          fontFamily="Garnett Regular"
          fontSize={30}
        >
          {subtitel2}
        </text>
        
        <text
          x={25}
          y={300}
          fill={'#ffffff'}
          textAnchor="start"
          fontWeight="regular"
          fontFamily="Garnett Regular"
          fontSize={72}
        >
          {titel}
          <tspan>
            
          </tspan>
        </text>

        <text
          x={25}
          y={385}
          fill={'#ffffff'}
          textAnchor="start"
          fontWeight="regular"
          fontFamily="Garnett Regular"
          fontSize={72}
        >
          {titel2}
        </text>

        <text
          x={25}
          y={575}
          fill={'#ffffff'}
          textAnchor="start"
          fontWeight="regular"
          fontFamily="Garnett Regular"
          fontSize={30}
        >
          {untertitel}
        </text>

        <text
          x={25}
          y={575}
          fill={'#ffffff'}
          textAnchor="start"
          fontWeight="regular"
          fontFamily="Garnett Regular"
          fontSize={20}
        >
          {sponsoring}
        </text>
       
    </svg>
  );
};

export const inputs = {
  topLogo: { 
    type: "image",
    multiple: false,
  },
  bottomLogo: { 
    type: "image",
    multiple: false,
  },
  subtitel: {
    type: "text",
    default: "Monday March 21st: From 11.00–11.45",
  },
  subtitel2: {
    type: "text",
    default: "Webinar",
  },
  titel: {
    type: "text",
    default: "Guidelines for Blockchain",
  },
  titel2: {
    type: "text",
    default: "and DLT Governance",
  },
  untertitel: {
    type: "text",
    default: "– the new standard explained",
  },
  sponsoring: {
    type: "text",
    default: "",
  },
};

